import { Component } from 'react'
import { ButtonPanel } from './Components/ButtonPanel.js'
import { MainContent } from './Components/MainContent.js'
import { StatusBar } from './Components/StatusBar.js'
import { Treat } from './Components/Treat.js'
import { TreatInventory } from './Components/TreatInventory.js'
import { Shop } from './Components/Shop.js'
import { ShopPrompt } from './Components/ShopPrompt.js'
import { Settings } from './Components/Settings.js'
import { PetSwap } from './Components/PetSwap.js'
import { MainContentTwo } from './Components/MainContentTwo.js'

export class Game extends Component {

    state = {
        pika: true, 
        chari: false, 
        treatInventory: false,
        game: false,
        shop: false,
        settings: false,
        treat: false,
        thirst: 5,
        hunger: 5,
        swap: false,
    }

    onTreatClickTwo = (event) => {
        let nav = 'nav-button '

        if (event.target.className == nav + 'one') {
            this.setState({
                treatInventory: true,
            }
            )
        } else if (event.target.className == nav + 'two') {
            this.setState({
                game: true,
            }
            )
        } else if (event.target.className == nav + 'four') {
            this.setState({
                shop: true,
            }
            )
        } else if (event.target.className == nav + 'five') {
            this.setState({
                settings: true,
            }
            )
        }

        // console.log("working")
    }

    onCloseOverlay = () => {
         
        this.setState({
            treatInventory: false,
            game: false,
            shop: false,
            settings: false,
            treat: false,
            swap: false,
        }
        )
    }


    OnTreatShop = () => {
        this.setState({
            shopPrompt: true
        })
    }

    onClickYes = () => {
        this.setState({
            shop: true,
            shopPrompt: false
        })

    }

    onTreatItem = () => {
        this.setState({
            treatInventory: false,
            treat: true
        })
    }

    onFeed = () => {
        this.setState({
            hunger: 3,
            treat: false
        }
        )
    }

    onSwapPet = () => {
        this.setState({
            swap: true
        }
        )
    }

    onChariSwap = () => {
        this.setState({
            pika: true,
            chari: false,
            swap: false,
            settings: false,
        }
        )
    }

    onPikaSwap = () => {
        this.setState({
            pika: false,
            chari: true,
            settings: false,
            swap: false
        }
        )
    }

    render() {
        return <div>
            <main>
                <div className="game-window">
                    <StatusBar props={this.state}/>
                    {this.state.pika && <MainContent/>}
                    {this.state.chari && <MainContentTwo/>}
                    <ButtonPanel onTreatClick={this.onTreatClickTwo} />
                    {this.state.treatInventory && <TreatInventory onOverlayClick={this.onCloseOverlay} onTreatItemTwo={this.onTreatItem} />}
                    {this.state.shop && <Shop onOverlayClick={this.onCloseOverlay} onShopItem={this.OnTreatShop}/>}
                    {this.state.shopPrompt && <ShopPrompt onYesButton={this.onClickYes}/>}
                    {this.state.settings && <Settings onOverlayClick={this.onCloseOverlay} onClickMenu={this.props.onClickGame} onSwap={this.onSwapPet}/>}
                    {this.state.treat && <Treat onFeedPet={this.onFeed}/>}
                    {this.state.swap && <PetSwap onOverlayClick={this.onCloseOverlay} onChari={this.onChariSwap} onPika={this.onPikaSwap} />}
                </div>
            </main>
        </div>
    }
}

