import { Component } from "react";

export class ShopPrompt extends Component {

    render() {
        return <div className="prompt-box">
            <div className="prompt-grid">
                <p>Are you sure you want to buy X item for X amount?</p>
                <div className="prompt-buttons">
                    <button onClick={this.props.onClickNo}>No</button>
                    <button onClick={this.props.onYesButton}>Yes</button>
                </div>
            </div>
        </div>

    }
}

