import { Component } from 'react'
import { Game } from './Games.js'
import { Menu } from './Menu.js'


export class MainGame extends Component {

  state = {
    game: false, 
    menu: true,

  }



  onOpenGame = () => {
         
    this.setState({
        game:true,
        menu: false,
    }
    )
}

onOpenMenu = () => {
         
  this.setState({
      game: false,
      menu: true,
  }
  )
}

  render() {
    return <div>
          {this.state.menu && <Menu onClickGame={this.onOpenGame}/>}
         {this.state.game && <Game onClickGame={this.onOpenMenu}/>}
    </div>}
}


