import { Component } from "react";

import Pikachu from '../Images/Pikachu.png'
import Charizard from '../Images/charizard.png'



export class PetSwap extends Component {

    render() {
        return  <div className="black-overlay">
        <div className="treat-container">
            <div className="treat-blocks">
                <div className="exitButton" onClick={this.props.onOverlayClick}>X</div>
                <h3>Pet Swap</h3>
                

                <div className="treatBlock" onClick={this.props.onPika}>
                    <img src={Charizard} />
                    <div className="desc">
                        <h4>Charizard</h4>
                        <p>Age: 10 minutes</p>

                    </div>
                    <div>
                        <h4>Hunger: Full</h4>
                        <h4>Hydration: Dehydrated</h4>
                    </div>

                </div>

                <div className="treatBlock" onClick={this.props.onChari}>
                    <img src={Pikachu} />
                    <div className="desc">
                        <h4>Pikachu</h4>
                        <p>Age: 5 minutes</p>

                    </div>
                    <div>
                        <h4>Hunger: Full</h4>
                        <h4>Hydration: Full</h4>
                    </div>

                </div>
                </div>
            </div>
        </div>

    }
}






